import {Link} from '../routes'

import { Typography, Toolbar, Button, AppBar, Grid } from '@material-ui/core'
import Work from '@material-ui/icons/Work';
import lightblue from '@material-ui/core/colors/blueGrey'

import PropTypes from 'prop-types'

class Footer extends React.Component {

    render() {
  
      const { classes } = this.props;
  
      return (
        <div style={{backgroundColor: lightblue[100]}}>
            <div className={classes.innerContainer}>
                <div className={classes.contentSection}>
                    <Grid container wrap="nowrap" spacing={32} className={classes.bigLineSpacing}>
                        <Grid item xs={4} className={classNames.footerIcon}>
                            <Work />
                        </Grid>
                    </Grid>
                </div>
            </div>
        </div>
      )
    }
}

Footer.propTypes = {
    classes: PropTypes.object.isRequired
}

export default Footer


